import seedrandom from "seedrandom";

export interface FuelValue {
  fuel: string;
  value: number;
}

export type SectorKey =
  | "07_total_primary_energy_supply"
  | "12_total_final_consumption"
  | "09_total_transformation_sector"
  | string;

export interface EnergyProfile {
  economy: string;
  name: string;
  chartImage?: string;
  source?: string;
  sectors: Record<SectorKey, FuelValue[]>;
  metrics?: {
    tpes?: number;
    tfc?: number;
    elec_gen?: number;
    net_imports?: number;
    net_imports_by_fuel?: FuelValue[];
  };
}

export interface EnergyDataset {
  year: number;
  scenario: string;
  sectors: SectorKey[];
  profiles: EnergyProfile[];
}

export interface EnergyDistance {
  distance: number;
  maxDistance: number;
  proximity: number;
}

export interface EnergyGuess {
  name: string;
  distance: number;
  proximity: number;
  total: number;
  totalProximity: number;
  production: number;
  tfc: number;
  elecGen: number;
  netImports: number;
}

